#header{
    position: relative;
    width: 100%;
    height: 50vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin-top: 5%;
}
#header img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    border-radius: 15px;
}
#header #moon{
    mix-blend-mode: screen;
    z-index: 8;
    animation: moon-an 1s;
}
#header #grass{
    z-index: 9;
    animation: grass-an 1s;
}
#header #grave{
    z-index: 11;
    animation: grave-an 1s;
}
#header #cloud{
    animation: cloud-an 1s;
}
@keyframes grass-an {
	0% {

        opacity: 0;
	}
	100% {

        opacity: 1;
	}
}
@keyframes grave-an {
	0% {
		transform: translateY(100%);
        opacity: 0;
	}
	100% {
		transform: translateY(0);
        opacity: 1;
	}
}
@keyframes cloud-an {
	0% {
		transform: translatex(100%);
        opacity: 0;
	}
	100% {
		transform: translateY(0);
        opacity: 1;
	}
}
@keyframes moon-an {
	0% {
        opacity: 0;
	}
	100% {
        opacity: 1;
	}
}
@keyframes button-an {
	0% {
        opacity: 0;
	}
	100% {
        opacity: 1;
	}
}
