}

.skull-icon{
    height: 24px;
}

.season {
    position: absolute;
    top: 100%;
    padding: 12px 18px;
    color: #fff;
    font-weight: 400;
    transform: scaleY(0);
    transform-origin: top;
    background-size: 400% 400%;
    animation: gradient 5s infinite;
    border: 2px solid var(--yellow);
    border-radius: 6px;
    background: #221b47;
    -webkit-backdrop-filter: blur(20px);
    backdrop-filter: blur(20px);
    z-index: 9;
    opacity: 0;
    min-width: 13vw;
    transition: opacity 0.54s linear 0s, transform 0s 0.54s;
}

.season .skull-button.view-more{
    font-size: 12px;
    font-weight: 500;
    padding: 2px 6px;
    width: 100%;
}

.balances-offcanvas {
    position: relative;
}

.balances-offcanvas:hover > .season{
    transform: scaleY(1);
    opacity: 1;
    transition: opacity 0.54s ease 0s;
}



@media (min-width:320px) and (max-width:640px) { 
