        z-index: 9999;
        transition: transform .9s ease, opacity 0.7s ease 0.2s;
    }
    #navbarNav{
        position: absolute !important;
        left: 0;
        top: 100%;
        width: 100%;
        height: 100vh;
    }
    .season{
