        width: 50vw;
        top: -18vh;
    }
    .balances{
        z-index: 9;
        border-top: 2px solid var(--yellow);
        display: flex;
        width: auto;
        flex-direction: row !important;
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100vw;
    }
}
.graveyard{
  background: fixed;
  overflow: hidden;
}
#graveyard{
  color: black  ;
  background-color: aliceblue ;
  transition: 1s;
}

.graveyard .header{
  width: 90%;
  max-width: 1920px;
  margin-top: 1%;
  flex-direction: row;
  display: flex;
  background-color: transparent;
}
.graveyard #filter-header{
width: 23%;
margin-right: 2%;

font-size: 3vh !important;
border-bottom: 1px solid black;
}
