/*GALLERY MODAL*/
.graveyard .skull-modal{
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  text-align: center;
  z-index: 1;
  background: rgba(0, 0, 0, 0.356)  !important;
  overflow-y: hidden;
  color: #000000;
}
.graveyard .modal-container{
  margin: auto;
  width: 40%;
  transition: 1s;
  background-color: aliceblue;
  animation: 1s slideInFromTop;
  transform: translateY(80%);
  box-shadow: 0 1px 1px hsl(0deg 0% 0% / 8%), 0 2px 2px hsl(0deg 0% 0% / 8%), 0 4px 4px hsl(0deg 0% 0% / 8%), 0 8px 8px hsl(0deg 0% 0% / 8%), 0 16px 16px hsl(0deg 0% 0% / 8%);
  border-radius: 15px;
  display: flex;
  flex-direction: row;
}
.graveyard .modal-container .logo{
  position: absolute;
  width: 5%;
  right: 0;
  bottom: 0;
}
.graveyard .modal-container .modal-img img{
  border-radius: 15px 0px 0px 15px;
  width: 100% !important;
  margin: auto;
}

.graveyard .modal-container .modal-img iframe{
  border-radius: 15px 0px 0px 15px;
  min-width: 100% !important;
  min-height: 500px;
  padding: 0;
}
.graveyard .modal-container .modal-img{
  border-radius:15px 0px 0px 15px;
  width: 40%;
  justify-content: center;
  align-items: center;
  vertical-align: middle;
  border-right: 2px solid black;
}

.graveyard .modal-container .modal-desc{
  margin: auto;
  text-align: left;
  width: 50%;
}
.graveyard .modal-container .modal-desc p{

  margin: 0  !important;
}
.graveyard .modal-container .modal-desc h1{

  margin: 0  !important;
  font-size: 2vh !important;
}
.graveyard .modal-container .modal-desc h2{

  margin: 0  !important;
  font-size: 1.5vh !important;
}
.graveyard .modal-container .modal-desc .desc-header{
  width: 96%;
  flex-direction: row;
  display: flex;
  margin-left: 2%;
  margin-right: 2%;
}
.graveyard .modal-container .modal-desc .desc-title{
  width: 80%;
}
.graveyard .modal-container .modal-desc .desc-icons{
  width: 20%;
  font-size: 2vh;
  margin: auto;
  padding-right: 3%;
  text-align: right;
}
.graveyard .modal-container .modal-desc .desc-icons a{
  margin: auto;
}

.graveyard .modal-container .modal-desc .attribute-container{
  width: 100% !important;
}
.graveyard .modal-container .modal-desc .attribute{
  width: 46%;
