/*SKULL CONTAINER*/
.graveyard .skull-container{
  width: 75%;
  height: 90%;
  overflow: auto;
}
.graveyard .skull-header h1{
  font-size: 3vh !important;
}
.graveyard .skull-header span{
  color: rgb(73, 73, 73);
  font-size: 3vh !important;
}
.graveyard .skull-row{
  width: 100%;
  padding-left: 15px;
  overflow: hidden;
  transition: 1s;
}
.graveyard .skull-row .skull-card{
  width: 18%;
  margin: 1%;
  margin-left: 1%;
  border-radius: 6px;
  animation: skullCardAnimation 1s;
}
@keyframes skullCardAnimation {
  0% {
transform: scale(0);
  }

  100% {
    transform: scale(1,1);
  }
}

@keyframes PopUpReverse {
  100% {
    transform: scale(0);
  }
}
@keyframes PopUp {
  0% {
    transform: scale(0);
      }
    
      100% {
        transform: scale(1,1);
      }
}


.graveyard .skull-row .skull-card .skull-img{
  width: 100%;
  border-radius: 6px;
